import { useEffect, useState } from 'react'
import { Button } from '../cmps/Button'
import locImg from '../assets/img/location.svg'
import { CloudsBackground } from '../cmps/CloudsBackground'
import { useSelector, useDispatch } from 'react-redux'
import { weatherService } from '../services/weather-service'
import { CitySearch } from '../cmps/CitySearch'
import { utilService } from '../services/util-service'

export const Sidebar = ({ currWeather, city }) => {
  const dispatch = useDispatch()
  const [isSearchOpen, setIsSearchOpen] = useState(false)
  const [weatherImage, setWeatherImage] = useState(null)
  const isCelcius = useSelector((state) => state.weatherModule.isCelcius)

  useEffect(() => {
    async function fetchWeatherImage() {
      switch (currWeather.main) {
        case 'Rain':
          setWeatherImage({
            class: 'rain-svg',
            src: await import('../assets/img/rainy.svg'),
          })
          break
        case 'Sunny':
          setWeatherImage({
            class: 'sunny-svg',
            src: await import('../assets/img/sunny.svg'),
          })
          break
        case 'Clear':
          setWeatherImage({
            class: 'sunny-svg',
            src: await import('../assets/img/sunny.svg'),
          })
          break
        case 'Clouds':
          setWeatherImage({
            class: 'cloud-svg',
            src: await import('../assets/img/cloudy.svg'),
          })
          break
        case 'Snow':
          setWeatherImage({
            class: 'snow-svg',
            src: await import('../assets/img/snow.svg'),
          })
          break

        default: {
        }
      }
    }
    fetchWeatherImage()
  }, [currWeather.main])

  const myLocationForecast = async () => {
    if (navigator?.geolocation) {
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          const { coords } = position
          const data = await weatherService.getWeatherByLoc(
            coords.latitude,
            coords.longitude
          )
          dispatch({ type: 'SET_CURR_WEATHER', currWeather: data })
        },
        (err) => {
          console.log(err, 'could not get your location')
        }
      )
    }
  }

  return (
    <>
      <div className="sidebar">
        <CitySearch isShow={isSearchOpen} setIsShow={setIsSearchOpen} />
        <CloudsBackground align="start" />
        <section className="sidebar-actions flex space-between">
          <Button
            text="Search for places"
            clickFunc={() => setIsSearchOpen((prevState) => !prevState)}
          ></Button>
          <Button
            iconSrc={locImg}
            round={true}
            clickFunc={myLocationForecast}
          ></Button>
        </section>
        <section className="sidebar-weather">
          {weatherImage ? (
            <img
              className={`svg-big ${weatherImage.class}`}
              src={weatherImage.src.default}
              alt=""
            />
          ) : (
            'Loading Image..'
          )}

          <div>
            <p className="degree">
            <span>
                {isCelcius ? (
                  <>
                    {currWeather.cTemp}{' '}
                    <span className="subtle">&#176;C</span>
                  </>
                ) : (
                  <>
                    {utilService.celToFahr(currWeather.cTemp)}{' '}
                    <span className="subtle">&#176;F</span>
                  </>
                )}
              </span>
            </p>
            <p className="flex column align-center feels-like">
              <span className="feels-like-txt subtle">feels like</span>
              <span className="feels-like-temp">
                {isCelcius ? (
                  <>
                    {currWeather.feelsLike}{' '}
                    <span className="subtle">&#176;C</span>
                  </>
                ) : (
                  <>
                    {utilService.celToFahr(currWeather.feelsLike)}{' '}
                    <span className="subtle">&#176;F</span>
                  </>
                )}
              </span>
            </p>
          </div>
          <p className="subtle status">{currWeather.main}</p>
          <p className="subtle flex column align-center time-loc">
            <span className="time">{Date(currWeather.dt).slice(0, 16)}</span>
            <span className="flex align-center loc">
              <img className="svg-xxsm loc-svg" src={locImg} alt="" />
              {city}
            </span>
          </p>
        </section>
        <CloudsBackground align="center" reverse={true} />
      </div>
    </>
  )
}
