import { utilService } from '../services/util-service'
import humidityIcon from '../assets/img/water-outline.svg'
import cloudIcon from '../assets/img/cloud.svg'
import eyeIcon from '../assets/img/eye.svg'
import windIcon from '../assets/img/wind.svg'
export const HighlightGrid = ({ currWeather }) => {
  return (
    <section className="today-highlights">
      <header>
        <h1>today's highlights</h1>
      </header>
      <div className="highlight-grid">
        <div className="flex column align-center card wind-status">
          <span className="card-title">
            wind status <img src={windIcon} alt="" />
          </span>
          <span className="xxl-font">
            {utilService.convertToKmPerHour(currWeather.wind)}{' '}
            <span className="subtle lg-font">kmh</span>
          </span>
        </div>
        <div className="flex column align-center card humidity">
          <span className="card-title">
            humidty <img src={humidityIcon} alt="" />
          </span>
          <span className="xxl-font">
            {currWeather.humidity} <span className="subtle lg-font">%</span>
          </span>
        </div>
        <div className="flex column align-center card visibility">
          <span className="card-title">
            visibility <img src={eyeIcon} alt="" />
          </span>
          <span className="xxl-font">
            {currWeather.visibility / 1000}{' '}
            <span className="subtle lg-font">kilometers</span>
          </span>
        </div>
        <div className="flex column align-center card cloudiness">
          <span className="card-title">
            cloudiness <img src={cloudIcon} alt="" />
          </span>
          <span className="xxl-font">
            {currWeather.clouds} <span className="subtle lg-font">%</span>
          </span>
        </div>
      </div>
    </section>
  )
}
