import { Button } from "./Button"
export const DegreeSwitch = ({changeToCelcius, changeToFahr, isCelcius}) => {

    return (
        <section className="flex align-center degree-switch">
            <Button color="whitesmoke" text="&#176;F" round={true} clickFunc={() => changeToFahr()} isActive={!isCelcius} isHover={false}></Button>
            <Button color="whitesmoke" text="&#176;C" round={true} isActive={isCelcius} clickFunc={() => changeToCelcius()} isHover={false}></Button>
        </section>
    )
}