import { useState, useCallback } from 'react'
import { Button } from './Button'
import backArrowImg from '../assets/img/back-arrow.svg'
import { weatherService } from '../services/weather-service'
import { utilService } from '../services/util-service'
import { useDispatch } from 'react-redux'
import searchIcon from '../assets/img/search-outline.svg'

export const CitySearch = ({ isShow, setIsShow }) => {

  const dispatch = useDispatch()
  const [suggestions, setSuggestions] = useState([])

  const handleChange = async (val) => {
    if(!val) {
      setSuggestions([])
      return
    }
    const data = await weatherService.getAutoCompleteData(val)
    data? setSuggestions(data) : setSuggestions([])
  }

  const setCurrWeather = async (place) => {
    const data = await weatherService.getWeatherByCity(place)
    dispatch({type: 'SET_CURR_WEATHER', currWeather: data})
    setIsShow(false)
  }

  const optimizedFn = useCallback(utilService.debounce(handleChange), [])

  return (
    <section className={`city-search ${isShow ? 'visible' : ''}`}>
      <Button
        iconSrc={backArrowImg}
        clickFunc={setIsShow}
        round={true}
        type="outline"
      ></Button>
      <label htmlFor="search">
        <input
          id="search"
          type="text"
          placeholder="Search a city.."
          onChange={(e) => optimizedFn(e.target.value)}
        />
        <img className='search-icon' src={searchIcon} alt=''/>
      </label>
      {suggestions.length > 0 && (
        <ul className="flex column clean-list autocomplete-list">
          {suggestions.map((place, i) => (
              <li className='autocomplete-place' key={i} onClick={(e) => setCurrWeather(e.target.innerText)}>{place}</li>
          ))}
        </ul>
      )}
    </section>
  )
}
