import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { utilService } from '../services/util-service'
export const DayCard = ({day, idx}) => {

    const [weatherImage, setWeatherImage] = useState(null)
    const isCelcius = useSelector((state) => state.weatherModule.isCelcius)

    useEffect(() => {
        async function fetchWeatherImage() {
          switch (day.main) {
            case 'Rain':
              setWeatherImage({
                class: 'rain-svg',
                src: await import('../assets/img/rainy.svg'),
              })
              break
            case 'Sunny':
              setWeatherImage({
                class: 'sunny-svg',
                src: await import('../assets/img/sunny.svg'),
              })
              break
            case 'Clear':
              setWeatherImage({
                class: 'sunny-svg',
                src: await import('../assets/img/sunny.svg'),
              })
              break
            case 'Clouds':
              setWeatherImage({
                class: 'cloud-svg',
                src: await import('../assets/img/cloudy.svg'),
              })
              break
            case 'Snow':
              setWeatherImage({
                class: 'snow-svg',
                src: await import('../assets/img/snow.svg'),
              })
              break
            default: {
                
            }
          }
        }
        fetchWeatherImage()
      }, [day.main])
    
      const timestampToDate = (timestamp) => {
        return new Date(timestamp).toDateString().slice(0, -4)
      }
    return (
        <li className="flex column align-center day-card">
            <p className='date'>{!idx? 'tomorrow' : timestampToDate(day.dt)}</p>
            <img className={`svg-xsm ${weatherImage?.class}`} src={weatherImage?.src?.default} alt="" />
            <p className='temp'>
                {isCelcius ? (
                  <>
                    {day.minTemp}{' '}
                    <span className="subtle">&#176;C ~ </span>
                  </>
                ) : (
                  <>
                    {utilService.celToFahr(day.minTemp)}{' '}
                    <span className="subtle">&#176;F ~ </span>
                  </>
                )}               
                {isCelcius ? (
                  <>
                    {day.maxTemp}{' '}
                    <span className="subtle">&#176;C</span>
                  </>
                ) : (
                  <>
                    {utilService.celToFahr(day.maxTemp)}{' '}
                    <span className="subtle">&#176;F</span>
                  </>
                )}
            </p>
        </li>
    )
}