
export const storageService = {
  query,
  getById,
  getByName,
  put,
}

async function query(entityType) {
  var entities = JSON.parse(localStorage.getItem(entityType)) || []
  return entities
}

async function getById(entityType, entityId) {
  try {
    const entities = await query(entityType)
   return entities.find(entity => entity.city.id === entityId)
  } catch (err) {
    throw new Error('Cannot get entity')
  }
}

async function getByName(entityType, entityName) {
    try {
      const entities = await query(entityType)
      const data = entities.find(entity => entity.city === entityName)
      const isOld = _isOldData(data?.currWeather.timestamp)
     return isOld? null : data
    } catch (err) {
      throw new Error('Cannot get entity')
    }
  }

