function _save(entityType, entities) {
  localStorage.setItem(entityType, JSON.stringify(entities))
}

function _isOldData(entityTimestamp) {
  if (!entityTimestamp) return false
  const now = Date.now()
  if (now - entityTimestamp >= 10800000) {
    console.log('updated!')
    return true
  }
  else return false
}