import { OPEN_WEATHER_API } from './secrets'
import { LOCATION_IQ_TOKEN } from './secrets'
import { storageService } from './async-storage-service'
import axios from 'axios'
import { utilService } from './util-service'

export const weatherService = {
  getWeatherByLoc,
  getWeatherByCity,
  getAutoCompleteData,
  getLastWeatherLocation,
}

const CITIES_STORAGE_KEY = 'citiesCache'
const CURR_WEATHER_KEY = 'currWeatherCache'

async function getWeatherByLoc(lat, lon) {
  try {
    const { data } = await axios.get(
      `https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&appid=${OPEN_WEATHER_API}`
    )
    const dataToSave = {
      _id: utilService.makeId(),
      city: data.city.name,
      coord: data.city.coord,
      currWeather: _prepCurrDayData(data),
      nextDays: _prepNextDaysData(data),
    }
    localStorage.setItem(CURR_WEATHER_KEY, JSON.stringify(dataToSave))
    return dataToSave
  } catch (err) {
    console.log('cannot get weather details by location from OPEN-WEATHER', err)
  }
}

async function getWeatherByCity(cityName) {
  cityName = cityName.toLowerCase()
  const savedCity = await storageService.getByName(CITIES_STORAGE_KEY, cityName)
  if (savedCity) {
    localStorage.setItem(CURR_WEATHER_KEY, JSON.stringify(savedCity))
    return savedCity
  } else {
    console.log('hi')
    try {
      const { data } = await axios.get(
        `https://api.openweathermap.org/data/2.5/forecast?q=${cityName}&appid=${OPEN_WEATHER_API}`
      )
      const dataToSave = {
        _id: utilService.makeId(),
        city: cityName,
        coord: data.city.coord,
        currWeather: _prepCurrDayData(data),
        nextDays: _prepNextDaysData(data),
      }
      storageService.put(CITIES_STORAGE_KEY, dataToSave)
      localStorage.setItem(CURR_WEATHER_KEY, JSON.stringify(dataToSave))
      return dataToSave
    } catch (err) {
      console.log(
        'cannot get weather details by city name from OPEN-WEATHER',
        err
      )
    }
  }
}

async function getAutoCompleteData(searchStr) {
  try {
    const { data } = await axios.get(
      `https://api.locationiq.com/v1/autocomplete?key=${LOCATION_IQ_TOKEN}&q=${searchStr}&tag=place:city&accept-language=en`
    )
    return data.map((place) => place.display_place)
  } catch (err) {
    console.log(err, 'failed to call autocomplete api')
  }
}

function getLastWeatherLocation() {
  const weatherData = JSON.parse(localStorage.getItem(CURR_WEATHER_KEY))
  return weatherData
}

function _prepCurrDayData({ list }) {
  return {
    cTemp: utilService.kelvinToCelsius(list[0].main.temp),
    feelsLike: utilService.kelvinToCelsius(list[0].main.feels_like),
    main: list[0].weather[0].main,
    humidity: list[0].main.humidity,
    clouds: list[0].clouds.all,
    visibility: list[0].visibility,
    wind: list[0].wind.speed,
    dt: +(list[0].dt + '000'),
    timestamp: Date.now(),
  }
}

function _prepNextDaysData({ list }) {
  const nextDaysWeather = []
  list.forEach((day, index) => {
    if (!(index % 7) && index) {
      nextDaysWeather.push({
        minTemp: utilService.kelvinToCelsius(day.main.temp_min - 3.1),
        maxTemp: utilService.kelvinToCelsius(day.main.temp_max),
        feelsLike: utilService.kelvinToCelsius(day.main.feels_like),
        main: day.weather[0].main,
        dt: +(day.dt + '000'),
      })
    }
  })
  return nextDaysWeather
}
