const INITIAL_STATE = {
    currWeather: null,
    isCelcius: true
}

export function weatherReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case 'SET_CURR_WEATHER':
            return {
                ...state,
                currWeather: action.currWeather
            }
        case 'SET_IS_CELCIUS':
            return {
                ...state,
                isCelcius: action.isCelcius
            }
        default:
           return state
    }
}