import { DegreeSwitch } from '../cmps/DegreeSwitch'
import {NextDaysForecast} from '../cmps/NextDaysForecast'
import { useSelector, useDispatch } from 'react-redux'
import {HighlightGrid} from '../cmps/HighlightGrid'
export const WeatherDetails = ({ nextDays, currWeather }) => {

  const dispatch = useDispatch()
  const isCelcius = useSelector(state => state.weatherModule.isCelcius)
  
  const changeToCelcius = () => {
      if(!isCelcius) dispatch({type: 'SET_IS_CELCIUS', isCelcius: true})
  }
  const changeToFahr = () => {
      if(isCelcius) dispatch({type: 'SET_IS_CELCIUS', isCelcius: false})
  }
  return (
    <div className="weather-details">
        <DegreeSwitch changeToCelcius={changeToCelcius} changeToFahr={changeToFahr} isCelcius={isCelcius}/>
        <NextDaysForecast nextDays={nextDays}/>
        <HighlightGrid currWeather={currWeather}/>
    </div>
  )
}
