import { Sidebar } from './main-cmps/Sidebar';
import { WeatherDetails } from './main-cmps/WeatherDetails';
import {weatherService} from './services/weather-service'
import './assets/styles/global.scss'
import { useEffect } from "react"
import {useDispatch, useSelector} from "react-redux"
function App() {

  const dispatch = useDispatch()
  const weather = useSelector((state => state.weatherModule.currWeather))

  useEffect(() => {
    async function getWeatherData() {
      const data = weatherService.getLastWeatherLocation() || await weatherService.getWeatherByCity('las vegas')
      console.log(data)
      dispatch({type: 'SET_CURR_WEATHER', currWeather: data})
    }
    if (!weather) getWeatherData()
  }, [weather, dispatch])

  if (!weather) return 'Loading...'
  else return (
    <div className="app-container">
          <Sidebar currWeather={weather.currWeather} city={weather.city}/>
          <WeatherDetails nextDays={weather.nextDays} currWeather={weather.currWeather}/>
    </div>
  );
}

export default App;
